import React, { useState } from 'react';
// Styled Components
import { FlipCardContainer, FlipCardBox, FlipCardFront, FlipCardBack, FlipCardImg, FlipCardTitle, FlipCardIcons, FlipCardIcon } from './FlipCard.styled';

const FlipCard = ({ config }) => {
  const {src, title, showGithub, showInternet, githubLink, internetLink, text} = config;
  const [isFlipped, setIsFlipped] = useState(false);

  const handleFlipClick = () => {
    setIsFlipped(!isFlipped);
  };

  return (
    <FlipCardContainer>
      <FlipCardBox className={isFlipped && 'flipped'}>

        <FlipCardFront>
          <FlipCardImg src={src} />
          <FlipCardTitle>{title}</FlipCardTitle>
          <FlipCardIcons>
            <FlipCardIcon src='/assets/icons/info.svg' onClick={handleFlipClick} />
            {showGithub && <a href={githubLink} target="_blank" rel="noopener noreferrer"><FlipCardIcon src='/assets/icons/github.svg' /></a>}
            {showInternet && <a href={internetLink} target="_blank" rel="noopener noreferrer"><FlipCardIcon src='/assets/icons/internet.svg' /></a>}
          </FlipCardIcons>
        </FlipCardFront>

        <FlipCardBack>
          <FlipCardIcon src='/assets/icons/github.svg' onClick={handleFlipClick} />
          <p>{text}</p>
        </FlipCardBack>
        
      </FlipCardBox>
    </FlipCardContainer>
  );
};

export default FlipCard;