import React, { useContext, useState } from 'react';
// Styled Components
import styled from 'styled-components';
// Components
import Description from '../components/description/Description';
import Button from '../components/button/Button';
import Experience from '../components/experience/Experience';
import Skills from '../components/skills/Skills';
// Context
import { LanguageContext } from '../contexts/LanguageContext';

const AboutButtons = styled.div`
    display: flex;
    justify-content: center;
    column-gap: 3rem;
    padding-block: .5rem;
`

const About = () => {
  const { languageData } = useContext(LanguageContext);
  const [selectedComponent, setSelectedComponent] = useState(null);

  const handleButtonClick = (component) => {
    setSelectedComponent(component);
  };


  return(
    <main>
      <Description />

      <AboutButtons>
        <Button icon='experience' onClick={() => handleButtonClick('experience')}>{languageData.buttons?.text1}</Button>
        <Button icon='skills' onClick={() => handleButtonClick('skills')}>{languageData.buttons?.text2}</Button>
      </AboutButtons>

      {selectedComponent === 'experience' && <Experience />}
      {selectedComponent === 'skills' && <Skills />}
    </main>
  )
}

export default About;