import React, { useContext } from 'react';
// Language Context 
import { LanguageContext } from '../../contexts/LanguageContext';
// Styled Components
import { Section, SectionTitleH1 } from '../../styles/base/Section.styled';
import { Container } from '../../styles/base/Container.styled';
// Components 
import ProjectSection from './ProjectSection';

const Project = () => {
  const { languageData, isLoading } = useContext(LanguageContext);

  const htmlProjects = [
    {
      title: "HTML Project 1",
      text: languageData.projects?.row1?.text1 || "",
      src: "/assets/img/projects/garcinstal.jpg",
      showGithub: true,
      showInternet: true,
      githubLink: "https://github.com/user/repo1",
      internetLink: "https://example.com/project1"
    },
    {
      title: "HTML Project 2",
      text: languageData.projects?.row1?.text2 || "",
      src: "/assets/img/projects/giriga.jpg",
      showGithub: false,
      showInternet: false
    },
    {
      title: "HTML Project 3",
      text: languageData.projects?.row1?.text3 || "",
      src: "/assets/img/projects/giriga.jpg",
      showGithub: false,
      showInternet: false
    },
    {
      title: "HTML Project 4",
      text: languageData.projects?.row1?.text4 || "",
      src: "/assets/img/projects/horizon.jpg",
      showGithub: true,
      showInternet: false,
      githubLink: "https://github.com/user/repo4"
    }
  ];

  const cssProjects = [
    {
      title: "CSS Project 1",
      text: languageData.projects?.row2?.text1 || "",
      src: "/assets/img/projects/garcinstal.jpg",
      showGithub: true,
      showInternet: true,
      githubLink: "https://github.com/user/repo1",
      internetLink: "https://example.com/project1"
    },
    {
      title: "CSS Project 2",
      text: languageData.projects?.row2?.text2 || "",
      src: "/assets/img/projects/giriga.jpg",
      showGithub: false,
      showInternet: false
    },
    {
      title: "CSS Project 3",
      text: languageData.projects?.row2?.text3 || "",
      src: "/assets/img/projects/giriga.jpg",
      showGithub: false,
      showInternet: false
    },
    {
      title: "CSS Project 4",
      text: languageData.projects?.row2?.text4 || "",
      src: "/assets/img/projects/horizon.jpg",
      showGithub: true,
      showInternet: false,
      githubLink: "https://github.com/user/repo4"
    }
  ];

  return (
    !isLoading &&
      <Section>
        <SectionTitleH1>Proyectos</SectionTitleH1>
        <Container>
          <ProjectSection 
            title="HTML" 
            navigationPrev="swiper-btn-prev-1" 
            navigationNext="swiper-btn-next-1" 
            projects={htmlProjects}
          />
          <ProjectSection 
            title="CSS" 
            navigationPrev="swiper-btn-prev-2" 
            navigationNext="swiper-btn-next-2" 
            projects={cssProjects}
          />
        </Container>
      </Section>
    
  );
};

export default Project;
