// Styled Component
import styled from 'styled-components';
import { Container } from '../../styles/base/Container.styled';
import { SectionTitleH1 } from '../../styles/base/Section.styled';
// Responsive
import device from '../../styles/responsive/breakpoints';

const HeroContainer = styled(Container)`
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    row-gap: 1.8rem;
    
    ${device.laptop`
        flex-wrap: nowrap;
        margin-top: 6rem;
    `}
`;

const HeroAvatar = styled.img`
    max-width: 400px;
    ${device.laptop`
        width: 400px;
    `}
`

const HeroData = styled.div`
    text-align: center;
    ${device.laptop`
        text-align: start;
        order: -1;
    `}
`;

const HeroTitle = styled(SectionTitleH1)`
    margin-bottom: 14px;
    text-transform: uppercase;
    font-size: clamp(19px, 3vw, 40px);

    ${device.laptop`
        text-align: start;
        margin-bottom: 40px;
    `}
`;

const HeroSubtitle = styled.h2`
    font-size: clamp(20px, 3vw, 26px);
    text-transform: uppercase;
    margin-bottom: 10px;
    font-weight: bold;
    ${device.laptop`
        margin-bottom: 40px;
    `}
`;

const HeroText = styled.p`
    margin-bottom: 30px;
    ${device.laptop`
        margin-bottom: 46px;
    `}
`;

export { HeroContainer, HeroAvatar, HeroData, HeroTitle, HeroSubtitle, HeroText }